.admin-panel {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  color: white;
}

.admin-panel h1 {
  text-align: center;
  margin-bottom: 2rem;
}

.users-table {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0,0,0,0.1);
}

.users-table table {
  width: 100%;
  border-collapse: collapse;
}

.users-table th,
.users-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #e1e5e9;
}

.users-table th {
  background: #667eea;
  color: white;
  font-weight: 600;
}

.users-table td {
  color: #333;
}

.users-table input,
.users-table select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.error-message {
  background: #ff6b6b;
  color: white;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 1rem;
  text-align: center;
} 